import { BaseElement } from '@umetrics/sartorius-ui-base';
import { Constructor } from './constructor';

/**
 * Mixin ResizeAwareMixin for Sartorius-UI baseclass
 *
 * Adds boiler code for the ResizeObserver
 *
 * Usage : class Demo extends ResizeAwareMixin(BaseElement) implements ... { ... }
 *
 * Default watch on this! Override __resizeObserverWatcher to watch something else, and then call _installResizeObserver at suitable point
 */

export declare abstract class ResizeAwareMixinInterface extends BaseElement {
    protected _resizeObserverElem();

    protected _installResizeObserver(elem);

    protected _uninstallResizeObserver(elem);

    protected abstract _resizeObserverCallback(entries: ResizeObserverEntry[]);
}

export const ResizeAwareMixin = <T extends Constructor<BaseElement>>(superClass: T) => {
    abstract class MyMixinClass extends superClass {
        private _resizeObserver;
        private _animationFrame: number | null = null;

        protected constructor(...args: any[]) {
            super(args);
            this._callback = this._callback.bind(this);
        }

        connectedCallback() {
            super.connectedCallback();
            if (this._resizeObserverElem() === null) {
                this._installResizeObserver(this);
            }
        }

        disconnectedCallback() {
            super.disconnectedCallback();
            this._uninstallResizeObserver(this);
        }

        protected _installResizeObserver(elem) {
            this._resizeObserver = new ResizeObserver(this._callback);
            this._resizeObserver.observe(elem);
            if (new Set(['inline', 'contents']).has(window.getComputedStyle(elem).display)) {
                throw new Error('ResizeObserver do nothing on inline elements');
            }
        }

        protected _uninstallResizeObserver(elem) {
            if (this._resizeObserver) {
                this._resizeObserver.unobserve(elem);
                this._resizeObserver.disconnect();
            }
        }

        /**
         * Override to watch other than this
         *
         * @protected
         */
        protected _resizeObserverElem() {
            return null;
        }

        /**
         * Override for callback implementation
         *
         * @param entries
         * @protected
         */
        protected abstract _resizeObserverCallback(entries: ResizeObserverEntry[]);

        private _callback(entries: ResizeObserverEntry[]) {
            if (!this._animationFrame) {
                this._animationFrame = window.requestAnimationFrame(() => {
                    this._animationFrame = null;
                    this._resizeObserverCallback(entries);
                });
            }
        }
    }

    return MyMixinClass as unknown as Constructor<ResizeAwareMixinInterface> & T;
};
