import {
    BaseElement,
    booleanConverter,
    createClassName,
    defineElement,
    html,
    property,
    query,
    state,
} from '@umetrics/sartorius-ui-base';
import { version } from './metadata';
import { tabsStyle } from './tabsStyle';
import { TabsProperties } from './tabsProperties';
import { TabEvents } from './tabEvents';
import { TabNotification } from './tabNotification';
import { Tab } from './tab';
import { TabsEvents } from './tabsEvents';
import { EventDispatcherMixin, ResizableBehaviorMixin, ResizeAwareMixin } from '@umetrics/sartorius-ui-mixins';

@defineElement({
    tagName: 'tabs',
    styles: tabsStyle,
    version,
    //    sub: [TooltipService]
})
export class Tabs
    extends EventDispatcherMixin(ResizeAwareMixin(ResizableBehaviorMixin(BaseElement)))
    implements TabsProperties
{
    // Static members
    public static BUTTON_SLOT = 'button-slot';

    private static TooltipService;
    private static TooltipServiceDelay;

    private static animations = {
        enter: {
            exitLeft: [
                {
                    transform: 'scaleX(1)',
                    transformOrigin: 'center left',
                    backgroundColor: '#000',
                },
                {
                    transform: 'scaleX(0)',
                    backgroundColor: 'unset',
                    transformOrigin: 'center left',
                },
            ],
            fromLeft: [
                {
                    backgroundColor: '#9D9D9D',
                },
                { backgroundColor: '#000' },
            ],
            fromRight: [
                {
                    backgroundColor: '#9D9D9D',
                },
                {
                    backgroundColor: '#000',
                },
            ],
            exitRight: [
                {
                    transform: 'scaleX(1)',
                    backgroundColor: '#000',
                    transformOrigin: 'center right',
                },
                {
                    transform: 'scaleX(0)',
                    backgroundColor: 'unset',
                    transformOrigin: 'center right',
                },
            ],
        },
    };

    // Public members
    @property({ type: String, reflect: true })
    heading = '';
    @property({ type: Boolean, converter: booleanConverter })
    navigation = false;
    @property({ type: Boolean, converter: booleanConverter })
    dense = false;
    @property({ type: Number, reflect: true })
    get value() {
        return this._value;
    }
    set value(val) {
        if (this._value !== val && val > -1 && val < this._tab.length) {
            this._openNewTab(this._tab[val]);
        }
    }
    @property({ type: Boolean, converter: booleanConverter, reflect: true, attribute: 'full-height' })
    fullHeight = false;
    @property({ type: Boolean, converter: booleanConverter, reflect: true })
    lined = false;

    // Private members

    @query('div[class$="-tabs"]')
    private _tabsDiv;
    @query('div[class*="-panel"]')
    private _panelDiv;
    @query('div[class*="-p-wrapper"]')
    private _panelWrapper;
    @query('[class*="-tooltip"]')
    private _tooltipElement;

    private _value = -1;
    @state()
    private readonly _tab: Tab[] = [];
    private _startX;
    private _scrollLeft;
    private _prevIndex = 0;
    private _shouldCenter = false;
    private _tooltipText = '';
    private _focusedElement;

    // Public methods
    constructor() {
        super();

        this._navigationClick = this._navigationClick.bind(this);
        this._tabRequestAddHandler = this._tabRequestAddHandler.bind(this);
        this._tabRequestOpenHandler = this._tabRequestOpenHandler.bind(this);
        this._onTabHover = this._onTabHover.bind(this);
        this._mouseDownHandler = this._mouseDownHandler.bind(this);
        this._mouseMoveHandler = this._mouseMoveHandler.bind(this);
        this._mouseUpHandler = this._mouseUpHandler.bind(this);
        this._mouseLeaveHandler = this._mouseLeaveHandler.bind(this);
        this._tabRequestStateHandler = this._tabRequestStateHandler.bind(this);

        this.addEventListener(TabEvents.Add, this._tabRequestAddHandler);
        this.addEventListener(TabEvents.RequestOpen, this._tabRequestOpenHandler);
        this.addEventListener('focusout', () => (this._focusedElement = null));
        this.addEventListener('keydown', this._handleKeydown);
        this.addEventListener(TabEvents.Change, () => this.requestUpdate());
        this.addEventListener(TabEvents.RequestStateChange, this._tabRequestStateHandler);
    }

    connectedCallback() {
        this.style.setProperty('display', 'none');
        const imports: any[] = [];

        imports.push({
            module: () => import('@umetrics/sartorius-ui-tooltip-service'),
            elem: ['TooltipService', 'TooltipServiceDelay'],
        });

        Tabs.dynSubRegister(imports, () => {
            super.connectedCallback();
            this.style.removeProperty('display');
        });
    }

    disconnectedCallback() {
        if (this._panelWrapper) {
            this._panelWrapper.removeEventListener('mousedown', this._mouseDownHandler);
        }
        this.removeEventListener('keydown', this._handleKeydown);
        this.removeEventListener('focusout', () => (this._focusedElement = null));
        document.removeEventListener('mouseup', this._mouseUpHandler);
        document.removeEventListener('mousemove', this._mouseMoveHandler);
        this._uninstallResizeObserver(this._panelDiv);
        super.disconnectedCallback();
    }

    protected firstUpdated() {
        this._tab.forEach((tab) => {
            if (tab.open) this._prevIndex = tab.index;
        });
        this._installResizeObserver(this._panelDiv);
    }

    protected update(changedProperties) {
        super.update(changedProperties);
    }

    // Before render

    protected render() {
        const [baseClassName, rootClasses] = createClassName(this.tagName.toLowerCase(), {
            navigation: this.navigation,
            fullHeight: this.fullHeight,
            dense: this.dense,
            lined: this.lined,
        });

        const viewHtml = this._tab.map((t, index) => {
            return this._addView(baseClassName, t, index);
        });

        const tooltipHtml = html`<${Tabs.TooltipService.tag} delay="${Tabs.TooltipServiceDelay.Short}" class="${baseClassName}-tooltip" location="top-right"><span>${this._tooltipText}</span></${Tabs.TooltipService.tag}>`;

        const headingHtml = this.heading ? html`<div class="${baseClassName}-heading">${this.heading}</div>` : html``;

        return html`
            <div class="${rootClasses}">
                <div class="${baseClassName}-p-wrapper">
                    <div class="${baseClassName}-left">${headingHtml}</div>
                    <div class="${baseClassName}-panel">
                        ${tooltipHtml}
                        <div class="${baseClassName}-tabs">${viewHtml}</div>
                        <div class="${baseClassName}-right">
                            <slot name=${Tabs.BUTTON_SLOT}></slot>
                        </div>
                    </div>
                </div>
                <div class="${baseClassName}-content">
                    <slot></slot>
                </div>
            </div>
        `;
    }

    protected override _resizeObserverElem() {
        return this._panelDiv;
    }

    protected override _resizeObserverCallback(entries: ResizeObserverEntry[]) {
        entries.forEach((entry) => {
            if (entry.contentRect.width < this._tabsDiv.scrollWidth) {
                this._panelWrapper.addEventListener('mousedown', this._mouseDownHandler);
                this._panelWrapper.addEventListener('mouseup', this._mouseLeaveHandler);
                this._panelWrapper.addEventListener('mouseleave', this._mouseLeaveHandler);
                this._panelDiv.style.cursor = 'grab';
                this._shouldCenter = true;
            } else {
                this._panelWrapper.removeEventListener('mousedown', this._mouseDownHandler);
                this._panelWrapper.removeEventListener('mouseup', this._mouseLeaveHandler);
                this._panelWrapper.removeEventListener('mouseleave', this._mouseLeaveHandler);
                this._panelDiv.style.cursor = 'unset';
                this._shouldCenter = false;
            }
        });
    }

    private _enterLeft(tab: Tab) {
        const border = tab.querySelector('div[class$="-button-border"]')!;
        const animationKeyframes = Tabs.animations['enter'].fromLeft;
        const animationOptions = {
            duration: 200,
            iterations: 1,
        };
        const fadeInAnimation = border.animate
            ? border.animate(animationKeyframes, animationOptions)
            : { onfinish: undefined };
        fadeInAnimation.onfinish = () => void 0;
    }

    private _enterRight(tab: Tab) {
        const border = tab.querySelector('div[class$="-button-border"]')!;
        const animationKeyframes = Tabs.animations['enter'].fromRight;
        const animationOptions = {
            duration: 200,
            iterations: 1,
        };
        const fadeInAnimation = border.animate
            ? border.animate(animationKeyframes, animationOptions)
            : { onfinish: undefined };
        fadeInAnimation.onfinish = () => void 0;
    }

    private _exitLeft(tab: Tab) {
        const border = tab.querySelector('div[class$="-button-border"]')!;
        const animationKeyframes = Tabs.animations['enter'].exitLeft;
        const animationOptions = {
            duration: 150,
            iterations: 1,
        };
        const fadeInAnimation = border.animate
            ? border.animate(animationKeyframes, animationOptions)
            : { onfinish: undefined };
        fadeInAnimation.onfinish = () => void 0;
    }

    private _exitRight(tab: Tab) {
        const border = tab.querySelector('div[class$="-button-border"]')!;
        const animationKeyframes = Tabs.animations['enter'].exitRight;
        const animationOptions = {
            duration: 150,
            iterations: 1,
        };
        const fadeInAnimation = border.animate
            ? border.animate(animationKeyframes, animationOptions)
            : { onfinish: undefined };
        fadeInAnimation.onfinish = () => void 0;
    }

    private _addView(baseClassName, tab, index) {
        const text = tab.heading ?? 'Tab ' + index;
        const tooltipText = tab.tooltip ?? text;

        const notificationTypeStr = tab.notificationType === TabNotification.Default ? 'default' : 'error';
        const notificationHtml =
            tab.notificationType !== TabNotification.None
                ? html`<div class="${baseClassName}-button-notification-${notificationTypeStr}"></div>`
                : null;

        const openStr = tab.open ? 'open' : 'closed';
        const stateStr = tab.disabled ? 'disabled' : openStr;
        const errorStr = tab.error ? 'error' : '';
        return html` <button
            title="${tooltipText}"
            tabindex=${tab.disabled || index !== 0 ? -1 : 0}
            custom-disabled="${stateStr}"
            ?disabled="${tab.disabled}"
            has-error="${errorStr}"
            @mouseover="${(e) => this._handleMouseOver(e, tab, index)}"
            class="${baseClassName}-button  ${baseClassName}-button-${stateStr}"
            @click="${tab.open || tab.disabled ? null : () => this._navigationClick(index)}"
        >
            ${notificationHtml}
            <div class="${baseClassName}-button-text">${text}</div>
            <div class="${baseClassName}-button-border"></div>
        </button>`;
    }

    private _handleKeydown(e) {
        if (e.target === this) {
            const arr = this.shadowRoot?.querySelectorAll('button');
            const elements: any = [].slice.call(arr);
            const removeItems: any = [];

            elements.forEach((elem) => {
                if (elem.getAttribute('custom-disabled') === 'disabled') {
                    removeItems.push(elem);
                }
            });
            removeItems.forEach((el) => {
                const removeIndex = elements.indexOf(el);
                elements.splice(removeIndex, 1);
            });

            if (e.key === 'ArrowRight') {
                this._handleArrowRight(e, elements);
            } else if (e.key === 'ArrowLeft') {
                this._handleArrowLeft(e, elements);
            }
        }
    }

    private _handleArrowRight(e, elements) {
        e.preventDefault();
        let currentIndex = this._focusedElement ? elements.indexOf(this._focusedElement) : 0;
        if (elements.length <= currentIndex + 1) {
            currentIndex = -1;
        }
        elements[currentIndex + 1].focus();
        elements[currentIndex + 1].tabindex = 0;
        this._focusedElement = elements[currentIndex + 1];
        elements[currentIndex >= 0 ? currentIndex : 0].tabindex = -1;
    }

    private _handleArrowLeft(e, elements) {
        e.preventDefault();
        let currentIndex = this._focusedElement ? elements.indexOf(this._focusedElement) : 0;
        if (currentIndex - 1 < 0) {
            currentIndex = elements.length;
        }
        elements[currentIndex - 1].focus();
        elements[currentIndex - 1].tabindex = 0;
        this._focusedElement = elements[currentIndex - 1];
        elements[currentIndex <= elements.length - 1 ? currentIndex : elements.length - 1].tabindex = -1;
    }

    private _handleMouseOver(e, tab, index) {
        if (tab.disabledTooltipText && tab.disabled) {
            this._onTabHover(e, tab.disabledTooltipText, index);
        } else if (tab.tooltipText) {
            this._onTabHover(e, tab.tooltipText, index);
        }
    }

    private _onTabHover(e, tooltipText, index) {
        if (e.target instanceof HTMLButtonElement) {
            this._tooltipText = tooltipText;
            this.requestUpdate();
            e.target.id = 'tooltip' + index;
            this._tooltipElement.objectId = 'tooltip' + index;
        }
    }

    private _navigationClick(index) {
        const arr = this.shadowRoot?.querySelectorAll('button');
        const elements: any = [].slice.call(arr);
        const selectedButton = elements[index];

        if (index >= this._prevIndex) {
            this._exitRight(elements[this._prevIndex]);
            this._enterLeft(selectedButton);
        } else {
            this._exitLeft(elements[this._prevIndex]);
            this._enterRight(selectedButton);
        }
        if (this._shouldCenter) {
            let hiddenLeft = 0;
            for (let i = 0; i < index; i++) {
                hiddenLeft += elements[i].getBoundingClientRect().width;
            }
            if (index > 0) {
                hiddenLeft -= 40;
            }
            this._tabsDiv.scroll({
                left: hiddenLeft,
                top: 0,
                behavior: 'smooth',
            });
        }
        if (this._value < 0) this._value = 0;
        this._tab[this._value].fold(() => {
            this._prevIndex = index;
            this._tab[index].expand(() => {
                this._setValue(index);
                this.notifyResize();
            });
        });
        this.requestUpdate();
    }

    private _tabRequestAddHandler(e: Event) {
        e.stopPropagation();
        const evt = e as CustomEvent;
        const element = evt.detail.element as Tab;

        if (this._tab.indexOf(element) > -1) {
            return;
        }

        if (element.open) {
            this._tab.forEach((tab) => {
                if (tab.open) {
                    tab.fold(() => element.expand(null));
                }
            });
        } else if (this._tab.length === 0) {
            element.expand(null);
        }

        if (element.index > -1) {
            // May collide if element index is not unique. Client fault!
            this._tab.splice(element.index, 0, element);
        } else {
            // No index set by client. Pick next "free"
            this._tab.push(element);
            element.index = this._tab.length === 1 ? 0 : this._tab[this._tab.length - 2].index + 1;
        }

        this._tab.sort((x, y) => (x.index < y.index ? -1 : 1));

        if (element.open) {
            this._setValue(element.index);
        }

        element.addEventListener(TabEvents.Remove, (e2) => this._tabRequestRemoveHandler(e2, element), { once: true });
        this.requestUpdate();
    }

    private _tabRequestRemoveHandler(e: Event, element) {
        e.stopPropagation();
        const index = this._tab.indexOf(element);
        if (index > -1) {
            this._tab.splice(index, 1);
        }
        if (element.open && this._tab.length > 0 && this._tab[0]) {
            this._tab[0].expand(null);
            this._setValue(0);
        }
        this.requestUpdate();
    }

    private _setValue(val) {
        this.setAttribute('value', '' + val);
        this._value = val;
    }

    private _tabRequestOpenHandler(e: Event) {
        const evt = e as CustomEvent;
        this._openNewTab(evt.detail.element);
        e.preventDefault();
        return false;
    }

    private _openNewTab(nextOpen) {
        const prevValue = this._value;
        const prevOpen = prevValue > -1 ? this._tab[prevValue] : null;
        if (prevOpen) {
            // return false or preventDefault in eventhandler to block tab-change
            if (this.dispatch(TabsEvents.Change, { prev: prevValue, value: this._tab.indexOf(nextOpen) })) {
                prevOpen.fold(() => {
                    nextOpen.expand();
                    this._setValue(this._tab.indexOf(nextOpen));
                });
            }
        } else {
            // First open
            nextOpen.expand();
            this._setValue(this._tab.indexOf(nextOpen));
        }

        this.requestUpdate();
    }

    private _mouseDownHandler(e) {
        this._tabsDiv.style.userSelect = 'none';
        this._panelDiv.style.cursor = 'grabbing';
        document.addEventListener('mousemove', this._mouseMoveHandler);
        document.addEventListener('mouseup', this._mouseUpHandler);
        this._startX = e.pageX - this._tabsDiv.offsetLeft;
        this._scrollLeft = this._tabsDiv.scrollLeft;
    }

    private _mouseMoveHandler(e) {
        const x = e.pageX - this._tabsDiv.offsetLeft;
        const walk = x - this._startX;
        this._tabsDiv.scrollTo({ left: this._scrollLeft - walk });
    }

    private _mouseLeaveHandler() {
        this._panelDiv.style.cursor = 'grab';
        this._tabsDiv.style.userSelect = 'unset';
        this._panelWrapper.removeEventListener('mousemove', this._mouseMoveHandler);
    }

    private _mouseUpHandler() {
        this._tabsDiv.style.userSelect = 'unset';
        document.removeEventListener('mousemove', this._mouseMoveHandler);
        document.removeEventListener('mouseup', this._mouseUpHandler);
    }
    private _tabRequestStateHandler() {
        this.requestUpdate();
    }
}
