import { CSSResultArray, cssSelector, GenerateStyles, style, css } from '@umetrics/sartorius-ui-base';

export const tabStyle: GenerateStyles = (tagName) => {
    const { root, is } = cssSelector(tagName);

    return [
        style,
        css`
            @keyframes expand {
                from {
                    opacity: 0;
                }

                to {
                    opacity: 1;
                }
            }
            :host,
            :host::before,
            :host::after,
            :host *,
            :host *::before,
            :host *::after {
                box-sizing: border-box;
            }

            ${root} {
                display: none;
            }

            ${is('open')} {
                animation: expand 0.5s;
                display: flex;
                height: 100%;
            }
        `,
    ] as CSSResultArray;
};
