import { css, CSSResultArray, cssSelector, cssVariableAsVar, GenerateStyles, style } from '@umetrics/sartorius-ui-base';

export const tabsStyle: GenerateStyles = (tagName) => {
    const { root, child, is } = cssSelector(tagName);

    return [
        style,
        css`
            :host,
            :host::before,
            :host::after,
            :host *,
            :host *::before,
            :host *::after {
                box-sizing: border-box;
            }

            :host {
                display: block;
                width: 100%;
            }

            :host([full-height]) {
                height: 100%;
            }

            ${is('fullHeight')} {
                display: flex;
                flex-direction: column;
                height: 100%;
            }

            ${is('fullHeight')} ${child('content')} {
                flex-grow: 1;
                height: calc(100% - 40px); /* Fix to get scrollbars. Flex does not! */
            }

            ${root} {
                width: 100%;
            }

            ${child('panel')} {
                display: none;
            }

            ${child('content')} {
                margin-top: 8px;
            }

            ${is('navigation')} ${child('panel')} {
                width: 100%;
                margin: 0;
                padding: 0;
                display: flex;
                flex-direction: row;
                min-height: 40px;
                align-items: flex-end;
                overflow: auto;
                justify-content: flex-end;
            }

            ${child('p-wrapper')} {
                display: flex;
                flex-direction: row;
                align-items: center;
                width: 100%;
            }

            ${child('tabs')} {
                flex-grow: 2;
                max-width: 100%;
                display: flex;
                flex-direction: row;
                overflow: hidden;
                min-height: 40px;
            }

            ${child('left')} {
                display: flex;
                flex-direction: row;
                justify-content: end;
                align-items: center;
                height: 100%;
            }

            ${child('right')} {
                display: flex;
                flex-grow: 2;
                flex-direction: row;
                justify-content: end;
                align-items: center;
                height: 100%;
            }

            ${child('button')} {
                display: flex;
                flex-direction: column;
                justify-content: end;
                align-items: center;
                position: relative;
                background: 0;
                outline: 0;
                border: 0;
                font-size: 14px;
                user-select: none;
                height: 36px;
                padding: 0;
            }

            ${child('button')}[has-error="error"] ${child('button-text')} {
                color: ${cssVariableAsVar('color_traffic_light_1')};
            }

            ${child('button-open')}[has-error="error"] ${child('button-border')} {
                background-color: ${cssVariableAsVar('color_traffic_light_1')} !important;
            }

            ${child('button-error')} {
                cursor: pointer;
                color: ${cssVariableAsVar('color_traffic_light_1')};
            }

            ${child('button')}:last-child {
                padding-right: 10px;
            }

            ${child('button-text')} {
                margin-left: 16px;
                font-weight: 500;
                margin-right: 16px;
                white-space: nowrap;
            }

            ${child('button-border')} {
                height: 3px;
                width: calc(100% - 32px);
                background: 0;
                margin-bottom: 0;
                margin-top: 5px;
            }

            ${is('dense')} ${child('button-notification-default')} {
                top: 37.5%;
                transform: translateY(-37.5%);
            }

            ${is('dense')} ${child('button-notification-error')} {
                top: 37.5%;
                transform: translateY(-37.5%);
            }

            ${child('button-notification-default')} {
                position: absolute;
                align-self: flex-end;
                margin-right: 10px;
                top: 52.5%;
                transform: translateY(-52.5%);
                right: -4px;
                height: 7px;
                width: 7px;
                background: black;
            }

            ${child('button-notification-error')} {
                position: absolute;
                align-self: flex-end;
                margin-right: 10px;
                top: 52.5%;
                transform: translateY(-52.5%);
                right: -4px;
                height: 7px;
                width: 7px;
                background: ${cssVariableAsVar('color_traffic_light_1')};
            }

            ${is('lined')} ${child('tabs')} {
                border-bottom: 1px solid ${cssVariableAsVar('color_secondary_3')};
                align-items: center;
            }
            ${is('lined')} ${child('button')} {
                height: 40px;
            }
            ${is('lined')} ${child('button-border')} {
                margin-top: 0px;
                width: 100%;
                height: 2px;
                transform: none;
                align-self: flex-end;
            }
            ${is('lined')} ${child('button-text')} {
                height: 100%;
                display: flex;
                align-items: center;
            }

            ${is('lined')} ${child('button-closed')}:nth-child(1) ${child('button-border')} {
                width: 100%;
                transform: none;
            }

            ${is('lined')} ${child('button-open')}:nth-child(1) ${child('button-border')} {
                width: 100%;
                transform: none;
            }
            ${is('lined')} ${child('button-open')}:nth-child(1) ${child('button-border')} {
                width: 100%;
                transform: none;
            }
            ${is('lined')} ${child('button-closed')}:nth-child(1) ${child('button-text')} {
                margin-left: 16px;
            }

            ${is('lined')} ${child('button-error')}:nth-child(1) ${child('button-text')} {
                margin-left: 16px;
            }

            ${is('lined')} ${child('button-open')}:nth-child(1) ${child('button-text')} {
                margin-left: 16px;
            }

            ${is('lined')} ${child('button')}:last-child {
                padding-right: 0px;
            }

            ${is('dense')} ${child('button')} {
                height: 24px;
            }

            ${is('dense')} ${child('button-border')} {
                margin-top: 2px;
            }

            ${child('heading')} {
                display: flex;
                padding-right: 10px;
            }

            ${child('button-open')} ${child('button-border')} {
                background-color: ${cssVariableAsVar('color_primary_3')};
            }

            ${child('button-open')} ${child('button-text')} {
                color: ${cssVariableAsVar('color_primary_3')};
            }

            ${child('button-closed')}:not([disabled]) {
                color: ${cssVariableAsVar('color_secondary_8')};
            }

            ${child('button-closed')}:not([disabled]):hover ${child('button-border')} {
                background-color: ${cssVariableAsVar('color_secondary_8')};
            }

            ${child('button-closed')}:not([disabled]):hover {
                cursor: pointer;
            }

            ${child('button-closed')}:nth-child(1) ${child('button-text')} {
                margin-left: 0;
            }

            ${child('button-error')}:nth-child(1) ${child('button-text')} {
                margin-left: 0;
            }

            ${child('button-open')}:nth-child(1) ${child('button-text')} {
                margin-left: 0;
            }

            ${child('button-closed')}:nth-child(1) ${child('button-border')} {
                width: calc(100% - 16px);
                transform: translateX(-8px);
            }

            ${child('button-open')}:nth-child(1) ${child('button-border')} {
                width: calc(100% - 16px);
                transform: translateX(-8px);
            }

            ${child('button-error')}:nth-child(1) ${child('button-border')} {
                width: calc(100% - 16px);
                transform: translateX(-8px);
            }

            ${child('button-disabled')} {
                color: ${cssVariableAsVar('color_secondary_4')};
            }

            ${child('button')}:focus-visible:before {
                content: ' ';
                display: block;
                position: absolute;
                inset: 0;
                border: 2px dashed black;
            }

            ::slotted([slot='button-slot']) {
                padding-left: 24px;
            }
        `,
    ] as CSSResultArray;
};
