import {
    BaseElement,
    booleanConverter,
    createClassName,
    defineElement,
    html,
    property,
    query,
    state,
} from '@umetrics/sartorius-ui-base';
import { version } from './metadata';
import { TabProperties, TabResolverFunc } from './tabProperties';
import { tabStyle } from './tabStyle';
import { TabEvents } from './tabEvents';
import { TabNotification } from './tabNotification';
import { EventDispatcherMixin } from '@umetrics/sartorius-ui-mixins';

@defineElement({ tagName: 'tab', styles: tabStyle, version })
export class Tab extends EventDispatcherMixin(BaseElement) implements TabProperties {
    // Static members

    // Public members
    @property({ type: String })
    get heading() {
        return this._heading;
    }
    set heading(val) {
        if (this._heading !== val) {
            const prev = this._heading;
            this._heading = val;
            if (!this._dispatchEvent(TabEvents.RequestStateChange, { element: this })) {
                this._heading = prev;
            }
        }
    }

    @property({ type: Number, reflect: true })
    index = -1;
    @property({ type: Boolean, converter: booleanConverter })
    get disabled() {
        return this._disabled;
    }
    set disabled(val) {
        if (this._disabled !== val) {
            this._disabled = val;
            if (!this._dispatchEvent(TabEvents.RequestStateChange, { element: this })) {
                this._disabled = !val;
            }
        }
    }
    @property({ type: Boolean })
    tooltip: string | undefined;
    @property({ type: String, reflect: false, attribute: 'disabled-tooltip-text' })
    disabledTooltipText = '';
    @property({ type: String, reflect: false, attribute: 'tooltip-text' })
    tooltipText = '';

    @property({ type: Function })
    tabResolver?: TabResolverFunc = undefined;
    @property({ type: TabNotification, attribute: 'notification-type', reflect: true })
    get notificationType() {
        return this._notificationType;
    }
    set notificationType(val: TabNotification) {
        if (this._notificationType !== val) {
            this._notificationType = val;
            this._dispatchEvent(TabEvents.Change, { element: this });
        }
    }
    @property({ type: Boolean, converter: booleanConverter })
    get error() {
        return this._error;
    }
    set error(val) {
        if (this._error !== val) {
            this._error = val;
            if (!this._dispatchEvent(TabEvents.RequestStateChange, { element: this })) {
                this._error = !val;
            }
        }
    }
    @property({ type: Boolean, converter: booleanConverter })
    get open() {
        return this._open;
    }
    set open(val) {
        if (this._open !== val) {
            if (this._dispatchEvent(val ? TabEvents.RequestOpen : TabEvents.RequestClose, { element: this })) {
                if (val) {
                    this.expand(null);
                } else {
                    this.fold(null);
                }
            }
        }
    }
    private _heading;
    private _disabled = false;
    private _notificationType: TabNotification = TabNotification.None;
    private _open = false;
    private _error = false;
    @query('slot')
    private readonly _slot!: HTMLSlotElement;

    @state()
    private _withContent = false;

    // Public methods
    connectedCallback() {
        super.connectedCallback();
        this._slotChanged = this._slotChanged.bind(this);

        this._dispatchEvent(TabEvents.Add, { element: this });
    }

    disconnectedCallback() {
        this._dispatchEvent(TabEvents.Remove);
        super.disconnectedCallback();
    }

    public fold(callback: ((e) => void) | null) {
        this._open = false;
        this.removeAttribute('open');
        this.requestUpdate();
        if (callback) {
            callback(null);
        }
    }

    public async expand(callback: ((e) => void) | null) {
        if (!this._withContent) {
            if (this.tabResolver !== undefined) {
                this.tabResolver(this).then((r) => {
                    this._tabResolutionCallback(r, callback);
                });
                return;
            }
            // Else no content... client need to handle change event and provide slot
        }
        this._open = true;
        this.setAttribute('open', 'true');
        this.requestUpdate();
        if (callback) {
            await this.updateComplete;
            callback(null);
        }
    }

    // Protected methods

    protected async firstUpdated(changedProperties) {
        super.firstUpdated(changedProperties);

        // Let browser paint before scheduling another update
        window.requestAnimationFrame(() => {
            /**
             * Due to safari bug we need to manually setup each slot
             */
            this._setupSlot(this._slot);
        });
    }

    protected render() {
        const [, rootClasses] = createClassName(this.tagName.toLowerCase(), {
            open: this.open,
        });
        return html` <div class="${rootClasses}"><slot @slotchange="${this._slotChanged}"></slot></div> `;
    }

    private _dispatchEvent(name: string, detail: any = null) {
        return this.dispatch(name, detail);
    }

    private _slotChanged(e: Event) {
        const target = e.target as HTMLSlotElement;
        this._setupSlot(target);
    }

    private _setupSlot(target: HTMLSlotElement) {
        const nodes = target && target.assignedNodes().filter((node) => node.nodeType === 1);
        this._withContent = nodes && nodes.length > 0;
    }

    private _tabResolutionCallback(r, callback) {
        if (typeof r === 'string') {
            this.insertAdjacentHTML('beforeend', r);
        } else if (r instanceof HTMLElement) {
            this.appendChild(r);
        }
        this._withContent = true;
        this.expand(callback);
    }
}
